body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
}

.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: stretch;
}

.block {
  height: 300px;
  width: 33%;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  flex-shrink: 0;
}

.block_1 {
  background-color: rgb(255, 0, 0);
}

.block_2 {
  background-color: rgb(204, 0, 0);
}

.block_3 {
  background-color: rgb(153, 0, 0);
}

.block_4 {
  background-color: rgb(104, 0, 0);
}

.block_5 {
  background-color: rgb(51, 0, 0);
}

.block_6 {
  background-color: rgb(0, 0, 0);
}

@media (min-width: 1200px) {
  .block_6 {
    order: 4;
  }

  .block_5 {
    order: 5;
  }

  .block_4 {
    order: 6;
  }
}

@media (max-width: 1200px) {
  .block_6 {
    order: 4;
  }

  .block_5 {
    order: 5;
  }

  .block_4 {
    order: 6;
  }
}

@media (max-width: 750px) {
  .block {
    width: 50%;
  }
 
  .block_6 {
    order: 6;
  }

  .block_5 {
    order: 5;
  }

  .block_4 {
    order: 3;
  }

  .block_3 {
    order: 4;
  }

}

@media (max-width:450px) {
  .block {
    order: 0;
    width: 100%;
  }
}
